import PageContext from './AppPageContext';
import PageContextItem, { PageContextItemState } from './PageContextItem';
import GuidHelpers from '../basicTypes/GuidHelpers';
import { requireServerRelativeUrl } from '../basicTypes/url';

/**
  * This interface is used with SPSite.load()
  */
export interface ISPSiteArgs {
  id: string;
  serverRelativeUrl: string;
  title: string;
}

/**
  * This class is primarily used with the PageContext class.  It provides contextual
  * information for the SharePoint site collection that hosts the page.
  */
export default class SPSite extends PageContextItem {
  private _id: string;
  private _serverRelativeUrl: string;
  private _title: string;

  constructor(pageContext: PageContext) {
    super(pageContext);

    this._id = undefined;
    this._serverRelativeUrl = undefined;
    this._title = undefined;
  }

  /**
   * Assigns all properties of the object, then assigns the "state" property
   * to PageContextItemState.Ready.
   */
  public load(args: ISPSiteArgs): void {
    this.validateLoad();

    GuidHelpers.requireValid(args.id);
    this._id = args.id;

    requireServerRelativeUrl(args.serverRelativeUrl);
    this._serverRelativeUrl = args.serverRelativeUrl;

    this._title = args.title;

    this.state = PageContextItemState.Ready;
  }

  /**
   * The GUID that identifies the SPSite on the server.
   */
  public get id(): string {
    return this._id;
  }

  /**
   * Returns a server-relative URL for this SPSite.
   * Example: "/sites/PubSite"
   */
  public get serverRelativeUrl(): string {
    return this._serverRelativeUrl;
  }

  /**
   * Returns the title of the current site collection.
   */
  public get title(): string {
    return this._title;
  }
}
